.progress-bar_moving{
	background: linear-gradient(45deg, #053B50, #0C356A, #176B87, #64CCC5);
	animation: gradient 15s ease infinite;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
